*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family:"Poppins",sans-serif;

}
#clock{
    display:flex;
    justify-content:center;
    align-items:center;
    min-height:100vh;
    background:#2f353e
}
#time{
    display:flex;
    gap:40px;
    color:#fff;
}

#time .circle{
    position:relative;
    width:150px;
    height:150px;
    display:flex;
    justify-content:center;
    align-items:center;
}
#time .circle svg{
    position:relative;
    width:150px;
    height:150px;
    transform:rotate(270deg)
}
#time .circle svg circle{
    fill:transparent;
    width:100;
    height:100;
    stroke:#191919;
    stroke-width: 4;
    transform:translate(5px,5px);
}

#time .circle svg circle:nth-child(2){
    stroke:var(--clr);
    stroke-dasharray: 440;
    stroke-dashoffset: var(--rt);
}
#time div{
    position:absolute;
    text-align:center;
    font-size:1.5em;
    font-weight:500;
}
#time div span{
    position:absolute;
    margin-top:5px;
    font-size: 0.35em;
    font-weight:300;
    letter-spacing:0.1em;
    text-transform:uppercase;
    transform:translateX(-50%) translateY(-10px);
}
#time .ap{
    position:relative;
    font:1em;
    transform:translate(-20px)
}

.dots{
    position:relative;
    width:100%;
    height:100%;
    z-index:10;
    display:flex;
    justify-content:center;
    
}
.dots::before{
    content:"";
    position:absolute;
    top:-3px;
    width:15px;
    height:15px;
    background:var(--clr);
    border-radius:50%;
    box-shadow:0 0 20px var(--clr),0 0 60px var(--clr);
}

.dots{
    transform:rotate(var(--tfrt));
}